<template>
  <div class="userdashboard">
    <div class="controls-bar">
      <select v-model="selectedSymbol" @change="resetData">
        <option value="BTC_USDT">BTC_USDT</option>
        <option value="SOL_USDT">SOL_USDT</option>
        <option value="DOGE_USDT">DOGE_USDT</option>
      </select>

      <input type="datetime-local" v-model="fromDate" @change="resetData" />

      <label class="toggle">
        <input type="checkbox" v-model="autoRefresh" />
        <span>Auto&nbsp;refresh</span>
      </label>

      <button v-if="!autoRefresh" @click="fetchMarketData">Load&nbsp;Data</button>
    </div>

    <BasicChart :market-data="marketData" :market-trades="marketTrades" />
  </div>
</template>

<script>
import axios from "axios";
import BasicChart from "./BasicChart.vue";

export default {
  components: { BasicChart },

  data() {
    const now = new Date();
    return {
      selectedSymbol: "BTC_USDT",
      fromDate: toLocalIso(datetimeMinus(now, { minutes: 60 })),

      marketData: [],
      marketTrades: [],

      autoRefresh: true,
      pollId: null
    };
  },

  watch: {
    autoRefresh(v) {
      v ? this.startPolling() : this.stopPolling();
    }
  },

  mounted() {
    this.startPolling();
  },

  beforeUnmount() {
    this.stopPolling();
  },

  methods: {
    /* -------- polling ---------- */
    startPolling() {
      if (this.pollId) return;
      this.fetchMarketData();                       // immediate
      this.pollId = setInterval(this.fetchMarketData, 2000);
    },
    stopPolling() {
      clearInterval(this.pollId);
      this.pollId = null;
    },

    resetData() {
      this.marketData = [];
      this.marketTrades = [];
      this.fetchMarketData();
    },

    /* -------- fetch ---------- */
    async fetchMarketData() {
      const sym = this.selectedSymbol;

      // last timestamp we already hold
      const lastMarketTs =
        this.marketData.length
          ? this.marketData[this.marketData.length - 1].tradedOn
          : this.fromDate;

      const lastTradeTs =
        this.marketTrades.length
          ? this.marketTrades[this.marketTrades.length - 1].tradedOn
          : this.fromDate;

      // use the later of the two as from
      const fromIso = laterIso(lastMarketTs, lastTradeTs);
      const toIso   = toLocalIso(new Date());              // now (local seconds)

      try {
        const md = await axios.get(
          `/api/Market/getMarketForSymbol/${sym}/${encodeURIComponent(fromIso)}/${encodeURIComponent(toIso)}`
        );
        const td = await axios.get(
          `/api/Market/getSimulatedTrades/${sym}/${encodeURIComponent(fromIso)}/${encodeURIComponent(toIso)}`
        );

        const mergedM = mergeById(this.marketData, md.data);
        const mergedT = mergeById(this.marketTrades, td.data);

        // replace arrays so Vue notices
        this.marketData   = mergedM;
        this.marketTrades = mergedT;
      } catch (e) {
        // eslint-disable-next-line no-console
        console.error("fetch error:", e);
      }
    }
  }
};

/* ---------- helpers ---------- */
function mergeById(existing, incoming) {
  const map = new Map(existing.map(o => [o.id, o]));
  incoming.forEach(o => map.set(o.id, o));
  // return sorted by timestamp
  return Array.from(map.values()).sort((a, b) =>
    a.tradedOn < b.tradedOn ? -1 : 1
  );
}

function laterIso(a, b) {
  return a > b ? a : b;
}

function datetimeMinus(d, diff) {
  return new Date(d.getTime() - toMs(diff));
}
function toMs({ seconds = 0, minutes = 0, hours = 0 }) {
  return ((hours * 60 + minutes) * 60 + seconds) * 1000;
}

/* local ISO (no Z) with seconds */
function toLocalIso(date) {
  const local = new Date(date.getTime() - date.getTimezoneOffset() * 60000);
  return local.toISOString().slice(0, 19);
}
</script>

<style scoped>
.userdashboard {
  background: #121212;
  color: #e0e0e0;
  font-family: Inter, Arial, sans-serif;
  padding: 20px;
  min-height: 100%;
  box-sizing: border-box;
}
.controls-bar {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  align-items: center;
  margin-bottom: 14px;
}
select,
input[type="datetime-local"] {
  background: #2e2e2e;
  border: 1px solid #444;
  color: #e0e0e0;
  padding: 6px 8px;
  border-radius: 4px;
  font-size: 14px;
}
button {
  background: #4e79a7;
  color: #fff;
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  transition: background 0.15s linear;
}
button:hover {
  background: #3d5f82;
}
.toggle {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 14px;
}
.toggle input {
  cursor: pointer;
}
</style>
