<template>
    <header class="header-bar">
      <img src="@/assets/logo.png" alt="Logo" class="logo"/> <!-- if you have a logo -->    
      <AuthComponent v-if="!userProfile" @user-authenticated="userAuthenticated" />
      <nav>
        <div v-if="userProfile">
          Welcome, {{ userProfile.name }}
        </div>      
      </nav>
    </header>
  </template>
  
  <script>
  import AuthComponent from './AuthComponent.vue';
  
  export default {
    components: {
      AuthComponent
    },
    props: {
      userProfile: Object // Define the type of the prop
    },
    methods: {
      userAuthenticated(profile) {
        this.$emit('user-authenticated', profile); // Emit an event instead of mutating the prop
      }
    }
  };
  </script>

<style scoped>
.header-bar {
    width: 100%;
    background-color: #333; /* Dark background */
    color: white;
    padding: 10px 20px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logo {
    height: 50px; /* Adjust as needed */
}

/* Add more styles for navigation and other elements */
</style>
