<template>
    <div class="auth-component" v-if="!userProfile">
      <input type="text" v-model="apiKey" placeholder="Enter API Key" @keyup.enter="login"/>
      <button @click="login">Login</button>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  
  export default {
    data() {
      return {
        apiKey: '',
        userProfile: null
      };
    },
    mounted() {
      this.checkAuthentication();
    },
    methods: {
      async checkAuthentication() {
        try {
          const response = await axios.post('/api/home');
          this.handleAuthenticationSuccess(response.data);
        } catch (error) {
          // If unauthorized, do nothing or handle it accordingly
          console.error('Error checking authentication status:', error);
        }
      },
      async login() {
        try {
            const response = await axios.post('/api/home', {}, {
                headers: { 'X-Api-Key': this.apiKey }
            });
    
            this.handleAuthenticationSuccess(response.data);
            } catch (error) {
          console.error('Authentication failed:', error);
        }
      },
      handleAuthenticationSuccess(userData) {
        this.userProfile = userData;
        this.$emit('user-authenticated', userData);
      }
    }
  };
  </script>
  